/* Portfolio Component Styles */

.portfolio-header {
  text-align: center;
  margin-bottom: 40px;
  padding: 20px;
}

.portfolio-header h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  letter-spacing: 2px;
}

.portfolio-header p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto 20px;
  line-height: 1.6;
}

.contact-info {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.contact-info span {
  font-size: 1rem;
  padding: 8px 16px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.portfolio-nav {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.nav-button {
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.nav-button:hover {
  transform: translateY(-2px);
}

/* Introduction Section */
.intro-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.background-card,
.education-card {
  padding: 30px;
  border-radius: 15px;
  line-height: 1.6;
}

.background-card h2,
.education-card h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.education-card h3 {
  font-size: 1.4rem;
  margin-bottom: 10px;
  font-weight: 600;
}

.education-card ul {
  margin-top: 15px;
  padding-left: 20px;
}

.education-card li {
  margin-bottom: 8px;
}

/* Experience Section */
.experience-section {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.experience-card {
  padding: 30px;
  border-radius: 15px;
  line-height: 1.6;
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  flex-wrap: wrap;
  gap: 10px;
}

.experience-header h3 {
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0;
}

.experience-header span {
  font-size: 1rem;
  font-weight: 500;
  padding: 4px 12px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
}

.experience-meta {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.experience-meta span {
  font-size: 0.9rem;
}

.experience-card ul {
  margin: 0;
  padding-left: 20px;
}

.experience-card li {
  margin-bottom: 8px;
}

/* Skills Section */
.skills-section {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
}

.skill-category {
  padding: 25px;
  border-radius: 15px;
}

.skill-category h3 {
  font-size: 1.4rem;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
}

.skill-tag {
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.skill-tag:hover {
  transform: translateY(-2px);
}

/* Company Logos Section */
.company-logos-container {
  margin-bottom: 30px;
}

.logos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.company-logo-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.company-logo-wrapper:hover {
  transform: translateY(-5px);
}

.company-logo-wrapper.selected {
  transform: translateY(-8px);
}

.logo-container {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.company-logo {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  filter: brightness(0) invert(1);
  transition: all 0.3s ease;
}

.company-info {
  text-align: center;
}

.company-name {
  font-size: 0.9rem;
  font-weight: 600;
  display: block;
  margin-bottom: 5px;
}

.company-period {
  font-size: 0.8rem;
  opacity: 0.8;
}

/* Personal Section */
.personal-section {
  max-width: 1000px;
  margin: 0 auto;
}

.personal-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: start;
}

.personal-image-container {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.image-wrapper {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
}

.personal-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.personal-info {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.personal-info h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.personal-items {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.personal-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.personal-item:hover {
  transform: translateX(5px);
}

.personal-item span:first-child {
  font-size: 1.5rem;
  min-width: 30px;
}

.personal-item span:last-child {
  font-size: 1rem;
  text-align: left;
}

.personal-description {
  margin-top: 20px;
  line-height: 1.6;
}

.personal-description p {
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .portfolio-header h1 {
    font-size: 2.5rem;
  }
  
  .portfolio-header p {
    font-size: 1rem;
  }
  
  .contact-info {
    flex-direction: column;
    gap: 15px;
  }
  
  .intro-section {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .experience-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .experience-meta {
    flex-direction: column;
    gap: 5px;
  }
  
  .skills-section {
    grid-template-columns: 1fr;
  }
  
  .personal-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .image-wrapper {
    height: 300px;
  }
  
  .personal-item {
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }
  
  .personal-item span:last-child {
    text-align: center;
  }
  
  .logos-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .portfolio-nav {
    flex-direction: column;
    align-items: center;
  }
  
  .nav-button {
    width: 200px;
  }
  
  .background-card,
  .education-card,
  .experience-card,
  .skill-category,
  .personal-card {
    padding: 20px;
  }
} 