.app-content {
  max-width: 1200px;
  margin: 0 auto;
}

.app-content h2 {
  margin-bottom: 20px;
  font-size: 2rem;
}

.app-content p {
  line-height: 1.6;
  margin-bottom: 30px;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin: 30px 0;
}

.feature-card {
  padding: 20px;
  border-radius: 8px;
  border: 1px solid;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.feature-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.feature-card h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.feature-card p {
  margin: 0;
  font-size: 0.9rem;
  opacity: 0.8;
}

.usage-example {
  margin-top: 40px;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid;
  background-color: rgba(0, 0, 0, 0.02);
}

.usage-example h3 {
  margin-bottom: 15px;
}

.usage-example pre {
  background-color: rgba(0, 0, 0, 0.05);
  padding: 15px;
  border-radius: 6px;
  overflow-x: auto;
  font-size: 0.85rem;
  line-height: 1.4;
  margin: 0;
}

code {
  background-color: rgba(0, 0, 0, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .feature-grid {
    grid-template-columns: 1fr;
  }
  
  .app-content h2 {
    font-size: 1.5rem;
  }
  
  .usage-example pre {
    font-size: 0.75rem;
  }
} 